.container{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: linear-gradient(#121212, #212b46);
}
.title{
    font-size: 85px;
    color: #fff;
    animation: flipflop 2s;
}

@keyframes flipflop {
    from{
        transform: rotateX(90deg);
    }
    to{
        transform: rotateX(0deg);
    }
}

.head{
    background-color: rgba(255, 255, 255, 0.2);
    padding: 15px;
    margin: 40px;
    display: flex;
    border-radius: 8px;
    box-shadow:1px 3px 8px rgba(0, 0, 0, 0.5);
}
.head > input{
    background-color: transparent;
    border: 0;
    font-size: 20px;
    color: #fff;
    outline: none;
    margin-right: 8px;
}
.head > input::placeholder{
    color: #f1f1f1;
}
.search{
    background-color: transparent;
    border: none;  
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: transform 0.5s;
}
.search:hover{
    transform: scale(1.2)
}
.main2{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #fff;
    width: 500px;
    border-radius: 8px;
}

.main2 > h2{
    margin: 16px 0;
    font-size: 20px;
}
.main > span {
    margin-bottom: 13px;
    font-weight: bold;
}

@media (max-width: 680px){
    .title{
        font-size: 50px;
        margin: 20px;
        padding: 10px;
        
    }
    .head{
        width: 70%;
    }
    .main2{
        width: 70%;
        min-height: 250px;
    }
}